.select {
    width: 100%;
    display: block;
    margin: 0.5rem 0;
    color: $color--main;
    border-radius: 2px;
    padding: 7px 12px;
    border: none;
    outline: none;
    background: $color--white url(./../images/select-arrow.svg) no-repeat 97% 50%;
    background-size: 9px 14px;
    box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
    -webkit-tap-highlight-color: transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}